/* global nextgenSiteContentData */
/**
 * External dependencies
 */
import { sortBy } from 'lodash';
import { ContentIcon as icon } from '@godaddy-wordpress/coblocks-icons';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { ComplementaryArea } from '@wordpress/interface';
import { registerPlugin } from '@wordpress/plugins';
import { Component } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';

/**
 * Local dependencies
 */
import { NextgenMenuIcon } from '@wpex/nextgen-common';
import PostTypePanel from './post-type-panel';
import { PLUGIN_NAME, SIDEBAR_NAME } from './constant';
import './style.scss';

export class NextGenSiteContent extends Component {
	loadPostIntoEditor = ( postType, postId ) => {
		if ( nextgenSiteContentData?.wp_version.indexOf( '5.7' ) === 0 && ! window.nextgen?.editSite ) {
			// TODO: This is temporary to prevent this module from breaking when WordPress 5.7 is released if our edit-site package is not ready.
			window.location.href = `/wp-admin/post.php?post=${ postId }&action=edit`;
		} else {
			// Disable the post publish button
			this.props.lockPostSaving();

			const postObject = this.props.getEntityRecord( 'postType', postType, postId );
			this.props.setupEditor( postObject, {} );

			// Re-enable the post publish button
			this.props.unlockPostSaving();
		}
	}

	render() {
		return (
			<>
				<ComplementaryArea
					scope={ window.nextgen?.editSite ? 'nextgen/edit-site' : 'core/edit-post' }
					className="content-management"
					identifier={ `${ PLUGIN_NAME }/${ SIDEBAR_NAME }` }
					title={ __( 'Site contents', 'nextgen' ) }
					smallScreenTitle={ __( 'Site contents', 'nextgen' ) }
					icon={
						<NextgenMenuIcon
							icon={ icon }
							label={ __( 'Site', 'nextgen' ) }
							slug="site-content" />
					}>

					{ this.props.postTypes.map( ( postType ) => (
						<PostTypePanel
							data-test="post-type-panel"
							key={ postType.slug }
							postType={ postType }
							loadPostIntoEditor={ this.loadPostIntoEditor } />
					) ) }
				</ComplementaryArea>
			</>
		);
	}
}

/* istanbul ignore next */
registerPlugin( PLUGIN_NAME, {
	icon,
	render: compose( [

		withSelect( ( select ) => {
			const {
				getCurrentPostId,
			} = select( 'core/editor' );

			const {
				getEntityRecord,
				getEntityRecords,
				getPostTypes,
			} = select( 'core' );

			const thePostTypes = ( getPostTypes() || [] )
				.filter( ( postType ) =>
					postType.viewable === true &&
					[ 'post', 'page' ].includes( postType.slug )
				);

			return {
				currentPostId: getCurrentPostId(),
				getEntityRecord,
				getEntityRecords,
				postTypes: sortBy( thePostTypes, [ 'name' ] ),
			};
		} ),

		withDispatch( ( dispatch ) => {
			const {
				lockPostSaving,
				setupEditor,
				unlockPostSaving,
			} = dispatch( 'core/editor' );

			return {
				lockPostSaving,
				setupEditor,
				unlockPostSaving,
			};
		} ),

	] )( NextGenSiteContent ),
} );
