/**
 * External dependencies
 */
import {
	PageDuplicateIcon,
	TrashConfirmIcon,
	TrashIcon,
} from '@godaddy-wordpress/coblocks-icons';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import {
	Button,
	Dropdown,
	Icon,
	MenuGroup,
	MenuItem,
} from '@wordpress/components';
import * as ICONS from './icons';
import classnames from 'classnames';

import { constant } from '@wpex/nextgen-common';
const { EID_PREFIX } = constant;

const PostMenuActions = ( props ) => {
	const [ isConfirming, setIsConfirming ] = useState( null );

	const handleInputKeys = ( event, onToggle ) => {
		if ( 'Escape' === event.key ) {
			onToggle();
		}
	};

	const PostMenuAction = ( { label, icon, onClickAction, modifierClass = null } ) => (
		<MenuItem
			onClick={ onClickAction }
			className={ `content-management-actions__item${ modifierClass ? ` ${ modifierClass }` : '' }` }>
			{ icon }
			{ label }
		</MenuItem>
	);

	return (
		<Dropdown
			className="content-management__panel__actions"
			contentClassName="content-management-actions"
			icon={ ICONS.ICON_COG }
			label={ __( 'Select an action', 'nextgen' ) }
			onKeyUp={ ( event, { onToggle } ) => handleInputKeys( event, onToggle ) }
			renderToggle={ ( { isOpen, onToggle } ) => (
				<Button
					className={ classnames( 'content-management__panel__actions__button', { 'is-open': isOpen } ) }
					onClick={ () => {
						onToggle();
						setIsConfirming( null );
					} }
					data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings/button.click` }
					aria-expanded={ isOpen }>
					{ ICONS.ICON_COG }
				</Button>
			) }
			renderContent={ ( { onClose } ) => (
				<MenuGroup>
					<PostMenuAction
						label={ __( 'Rename', 'nextgen' ) }
						icon={ ICONS.ICON_RENAME }
						data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings.rename/click` }
						onClickAction={ () => {
							props.onRenamePost();
							onClose();
						} } />

					{ props.shouldDisplayHomepageAction && (
						<>
							{ isConfirming && isConfirming === 'homepage' ? (
								<PostMenuAction
									label={ __( 'Really set as homepage?', 'nextgen' ) }
									icon={ ICONS.ICON_HOME }
									modifierClass="is-confirming"
									data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings.really_set_as_homepage/click` }
									onClickAction={ () => {
										props.onSetAsHomePost();
										setIsConfirming( null );
										onClose();
									} } />
							) : (
								<PostMenuAction
									label={ __( 'Set as homepage', 'nextgen' ) }
									icon={ ICONS.ICON_HOME }
									data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings.set_as_homepage/click` }
									onClickAction={ () => setIsConfirming( 'homepage' ) } />
							) }
						</>
					) }

					{ props.shouldDiplayPinAction &&
						<PostMenuAction
							label={ props.isSticky ? __( 'Unpin post', 'nextgen' ) : __( 'Pin post', 'nextgen' ) }
							icon={ ICONS.ICON_PIN }
							data-eid={ props.isSticky ? `${ EID_PREFIX }.site_contents/${ props.postType }/settings/pin_post.click` : `${ EID_PREFIX }.site_contents/${ props.postType }/settings/unpin_post.click` }
							onClickAction={ () => {
								props.onPinPost();
								onClose();
							} } />
					}

					<PostMenuAction
						label={ __( 'Duplicate', 'nextgen' ) }
						icon={ <Icon icon={ PageDuplicateIcon } /> }
						data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings.duplicate/click` }
						onClickAction={ () => {
							props.onDuplicatePost();
							setIsConfirming( false );
							onClose();
						} } />

					{ isConfirming && isConfirming === 'delete' ? (
						<PostMenuAction
							label={ __( 'Really delete?', 'nextgen' ) }
							icon={ <Icon icon={ TrashConfirmIcon } /> }
							modifierClass="is-destructive"
							data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings.delete_${ props.postType }/really_delete.click` }
							onClickAction={ () => {
								props.onDeletePost();
								setIsConfirming( null );
								onClose();
							} } />
					) : (
						<PostMenuAction
							label={ props.postType === 'page' ? __( 'Delete Page', 'nextgen' ) : __( 'Delete Post', 'nextgen' ) }
							icon={ <Icon icon={ TrashIcon } /> }
							modifierClass="is-destructive"
							data-eid={ `${ EID_PREFIX }.site_contents/${ props.postType }/settings.delete_${ props.postType }/click` }
							onClickAction={ () => setIsConfirming( 'delete' ) } />
					) }
				</MenuGroup>
			) }>
		</Dropdown>
	);
};

export default PostMenuActions;
